@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@400;600;700&display=swap');

:root {
    --color-main: #f68657;
    --color-text: #383a3f;
    --color-dark: #1f2124;
    --color-gray: #677;
    --color-bg: #f3f6f9;
    --color-light: #acb4bd;
    --color-lighter: #f9f9f9;
    --color-white: #fff;
    --color-border:#e0e3e6;

    font-size: 62.5%;
}

.dark {
    --color-main: #f68657;
    --color-text: #d6d1d1;
    --color-dark: #f5f6f7;
    --color-gray: #999;
    --color-bg: #1f2124;
    --color-lighter: #292a2c;
    --color-white: #2e3235;
    --color-border:#252629;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lexend', sans-serif;
    font-size: inherit;
    color: inherit;
    scroll-behavior: smooth;
}

body {
    line-height: 1.8;
    font-weight: 400;
    font-size: 1.6rem;
}

a {
    text-decoration: none;
}

.container {
    width: 100%;
    height: 100vh;
    color: var(--color-text);
    background-color: var(--color-bg);
    display: flex;
    align-items: center;
}

.app {
    width: 100%;
    max-width: 480px;
    height: 88vh;
    margin: 0 auto;
    background-color: var(--color-white);
    box-shadow: 1px 1px 6px rgba(0, 0, 0, .5);
}

.app-header {
    display: flex;
    align-items: center;
    padding: 1.6rem;
    justify-content: space-between;
    background-color: var(--color-lighter);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.app-header h1 {
    font-size: 3rem;    
    color: var(--color-dark);
    font-weight: 800;
    text-align: center;
}

.app-header button {
    border: 0;
    background: transparent;
    cursor: pointer;
}

.app-header button > svg {
    fill: var(--color-dark);
    height: 2.5rem;
    width: 2.5rem;
    object-fit: cover;
    cursor: pointer;
}

.app-body {
    padding: 1.6rem;
}

.notes-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1.6rem;
}

.notes-title,
.notes-count {
    color: var(--color-main);
    font-size: 2.4rem;
    font-weight: 600;
}

.notes-count {
    font-size: 1.8rem;
    color: var(--color-gray);
}

.notes-list-item {
    border-bottom: 1px solid var(--color-border);
    margin-bottom: 1.2rem;
    padding: .8rem 1.6rem;
    transition: all .2 ease-in-out;
}

.notes-list-item:hover {
    background-color: var(--color-bg);
    cursor: pointer;
}

.notes-list-item h3,
.notes-lit-item p span {
    font-weight: 600;
}

.notes-list-item p span {
    color: var(--color-gray);
    display: inline-block;
    margin-right: .8rem;
}

.notes-list-item p {
    font-size: 1.4rem;
    color: var(--color-light);
}

.floating-button {
    font-size: 4.8rem;
    position: absolute;
    bottom: 2.4rem;
    right: 1.6rem;
    background-color: var(--color-main);
    border: none;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
}

.floating-button > svg {
    fill: var(--color-bg);
}

.note-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--color-main);
    padding: 1rem;
}

.note-header h3 {
    fill: var(--color-main);
    width: 2rem;
    margin-right: .8rem;
}

.note-header button {
    border: none;
    outline: none;
    font-weight: 600;
    background-color: transparent;
    font-size: 1.8rem;
    cursor: pointer;
}

.note-header svg {
    cursor: pointer;
}

.note textarea {
    background-color: var(--color-white);
    border: none;
    padding: 1.6rem 1.2rem;
    width: 100%;
    height: 70vh;
    resize: none;
    scrollbar-width: none;
} 

.note textarea:active,
.note textarea:focus {
    outline: none;
    border: none;
}

.note textarea::-webkit-scrollbar {
    display: none;
}